<style lang="less">
</style>
<template>
  <div>
    <a-timeline mode="alternate">
      <a-timeline-item v-for='(item,index) in records' :key='index'>
        <p>日期: {{ item.date }}</p>
        <p>操作者: {{ item.username }}</p>
        <p>备注：{{ item.remark }}</p>
      </a-timeline-item>
    </a-timeline>
  </div>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import {getUserById} from "@/api/laisonorder";
import {getRecordsByCondition} from "@/api/laisonorder";

export default {
  name: "order-detail_model",
  mixins: [JeecgListMixin],
  props: {
    operateDetail: {
      type: Object,
      required: true
    }
  },
  mounted() {
    this.getUserName()
  },
  data() {
    return {
      records: []
    }
  },
  created() {
  },
  watch: {
    operateDetail: function (newval, oldName) {
    }
  },
  methods: {
    getUserName() {
      if (this.operateDetail && this.operateDetail.processData) {
        let orderRecord = this.operateDetail.processData.operation_records
        console.log()
        this.records = []
        for (let i = 0; i < orderRecord.length; i++) {
          this.getNameFromServer(orderRecord[i])
        }
        // this.getUserRecords(orderRecord)
      }
    },
    getUserRecords(data) {
      getRecordsByCondition(data).then(res => {
        this.records = res.result
      }).catch(err => {
        this.showErrorMsg(err.errmsg)
      })
    },

    getNameFromServer(order) {
      getUserById(order.userid).then((res) => {
        if (res) {
          order.username = res.result.name
          this.records.push(order)
          this.records.sort(this.compare("date", "inverted"))
        } else {
          this.showErrorMsg("获取数据失败!")
        }
      }).catch((err) => {
        this.showErrorMsg(err.errmsg, err.errcode)
      })
    },
    showErrorMsg(msg, code) {
      let errorMsg
      if (code && code != null) {
        errorMsg = "errorCode：" + code + " , errorMsg：" + msg
      } else {
        errorMsg = msg
      }
      this.$message.error(errorMsg)
    },
    compare(prop, align) {
      return function (a, b) {
        var value1 = a[prop];
        var value2 = b[prop];
        if (align == "positive") {//正序
          return new Date(value1) - new Date(value2);
        } else if (align == "inverted") {//倒序
          return new Date(value2) - new Date(value1);
        }
      }
    },
    fun(a,b,c,d){
      
    }
  }
}
</script>
